* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.icons {
    font-size: 32px;
    height: 32px;
    width: 32px;
    color: #ddd;
    cursor: pointer;
}

.icons:hover {
    color: red;
}

.icon-con {
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 50%;
    padding: 7px;
    background-color: #fff;
    margin: 7px;
}

.links {
    text-decoration: none;
    font-size: 24px;
    color: #002f34;
    font-weight: bold;
}

.price-text {
    color: #002f34;

}

.clear-btn {
    height: 24px;
    background-color: #002f34;
    color: #fff;
    border-radius: 4px;
    font-size: 12px;
    outline: none;
    border: none;
    padding: 0px 15px;
    margin-top: 10px;
    margin-left: 10px;
}

.card {
    background-color: #f5f2eb;
}

.fix {
    text-align: center; 
    display: flex;
    justify-content: center; 
    align-items: center; 
    gap: 20px; 
    font-size: 28px;
    margin-top: 15px;
}

.clear-btn {
    padding: 15px 70px;
    display: flex;
    justify-content: center; /* Horizontally center text */
    align-items: center; /* Vertically center text */
    text-align: center; 
}
